import React, { useEffect, useState } from 'react';
import { Link } from 'react-router-dom';
import connect from '@vkontakte/vk-connect';

import HeaderComponent from 'components/HeaderComponent';
import SettingsList from '../SettingsList';
import { Math } from 'core-js';

const mock = [
    {
        id: '1',
        title: 'first widget',
        type: 'personal',
        public: false
    },
    {
        id: '2',
        title: 'second widget',
        type: 'personal',
        public: false
    }
]

const Settings = () =>  {
    const [widgetList, setWidget] = useState(mock);
    const [token, setToken] = useState(null);

	const url = window.location.search;

    useEffect(() => {
        const params = new URLSearchParams(url);
        console.log('params', params);
        connect.subscribe(({detail: {type, data}}) => {
            if(type === 'VKWebAppCommunityAccessTokenResult') {
                setToken(data.access_token);
            }
        });
    }, []);

    console.log('token', token);

    const handleEdit = (id) => console.log('Edit', id);

    const handleCopy = (id) => {
        const widget = widgetList.find(item => item.id === id);
        const objCopy = Object.assign({}, widget);
        const random = Math.floor(Math.random() * Math.floor(100));
        const string = String(random);
        objCopy.id = string;
        setWidget([...widgetList, objCopy]);
    };

    const handleDelete = (id) => {
        setWidget(widgetList.filter(item => item.id !== id));
    };

    async function getToken() {
        await connect.sendPromise("VKWebAppGetCommunityAuthToken", {
            "app_id": 7206499, "group_id": 82607254, "scope": "app_widget"
        });
    }

    return (
        <div>
            <HeaderComponent title="Мои виджеты">
                <button><Link to="/widget">Создать виджет</Link></button>
                <button type="button" onClick={getToken}>Получить токен</button>
            </HeaderComponent>
            <SettingsList 
                handleEdit={handleEdit} 
                handleCopy={handleCopy} 
                handleDelete={handleDelete} 
                widgetList={widgetList} 
            />
        </div>
    );
};

export default Settings;