import React from 'react';
import { Route, Switch } from 'react-router-dom';

import Header from 'components/Header';
import Layuot from 'components/layout/Layout'; 
import Navigation from 'components/Navigation';
import WidgetsConfiguration from 'containers/WidgetConfiguration';

import {commonRoutes} from 'utils/routes';

const Application = () => (
    <Switch>
        <>
            <Header />
            <Route path="/configuration/:type">
                <Layuot middleBar={WidgetsConfiguration} />
            </Route>
            {commonRoutes.map(route => (
                <Route key={route.path} path={route.path}>
                    <Layuot middleBar={route.component} leftBar={<Navigation routes={commonRoutes} />} />
                </Route>
            ))}
        </>
    </Switch>
);

export default Application;